import { useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  setDroppedObjectData,
  setLoading,setObselenceObjectData,
  setIsDropped as setIsDroppedAction,
} from "../store/droppedObjectSlice";
import useToast from "../hooks/useToast";
import useObsolescenceWidget from "./useObsolescenceWidget";
import {
  initializeDroppableArea as initDroppable,
  getDroppedObjectDetails,
} from "../services/api/droppableService";
import {
  MSG_FETCH_OBJECT_DETAILS_FAILED,
  MSG_UNEXPECTED_ERROR,
} from "../utils/toastMessages";

const useObsolescenceDropableArea = () => {
  const { showErrorToast } = useToast();
  const { handleObsolescenceWidget } = useObsolescenceWidget();
  const isDropped = useSelector((state) => state.droppedObject.isDropped);
  const loading = useSelector((state) => state.droppedObject.loading);
  const dispatch = useDispatch();

  const validTypes = ["VPMReference", "Physical Product", "Change Action"];

  const fetchObjectDetails = useCallback(
    async (dataItems) => {
      try {
        console.log(
          "[FetchObjectDetails] Requesting with dataItems:",
          dataItems
        );

        const objectDetailsResult = await getDroppedObjectDetails({
          dataItems,
        });

        console.log("[FetchObjectDetails] API response:", objectDetailsResult);

        if (objectDetailsResult.success) {
          const draggedObjectData = objectDetailsResult?.data?.cardData;
          const objectType = draggedObjectData?.Type;
          const maturityState = draggedObjectData?.["Maturity State"];

          console.log("[Dragged Object Type]:", objectType);
          console.log("[Maturity State]:", maturityState);

          // if (!validTypes.includes(objectType)) {
          //   showErrorToast("Please select a CA or Engg Item.");
          //   dispatch(setLoading(false));
          //   return;
          // }

          // if (objectType === "Change Action" && maturityState === "Completed") {
          //   showErrorToast("CA is already completed.");
          //   dispatch(setLoading(false));
          //   return;
          // }

          // if (objectType === "Physical Product" && maturityState !== "Released") {
          //   showErrorToast("Product is not released.");
          //   dispatch(setLoading(false));
          //   return;
          // }
          console.log("[Validation] Object Type:", objectType);
          console.log("[Validation] Maturity State:", maturityState);
          console.log(
            "[Validation] Is valid type?",
            validTypes.includes(objectType)
          );

          if (!validTypes.includes(objectType)) {
            console.log("[Validation] Invalid object type error triggered");
            showErrorToast("Please select a CA or Engg Item.");
            dispatch(setLoading(false));
            return;
          }

          if (objectType === "Change Action" && maturityState === "Completed") {
            console.log("[Validation] CA completed error triggered");
            showErrorToast("CA is already completed.");
            dispatch(setLoading(false));
            return;
          }

          if (
            objectType === "Physical Product" &&
            maturityState !== "Released"
          ) {
            console.log("[Validation] Product not released error triggered");
            showErrorToast("Product is not released.");
            dispatch(setLoading(false));
            return;
          }

          dispatch(setDroppedObjectData({ cardData: draggedObjectData }));
          dispatch(setIsDroppedAction(true));
          console.log("[FetchObjectDetails] Setting dropped object data:", draggedObjectData);
          
          await handleObsolescenceWidget(
            draggedObjectData["Collaborative Space"], // collabSpace
            dataItems[0]?.objectId,                   // objectId
            dataItems[0]?.objectType,                 // objectType
            draggedObjectData["relativePath"]         // relativePath
          );          
        } else {
          console.error(
            "[FetchObjectDetails] API failure:",
            objectDetailsResult
          );
          showErrorToast(MSG_FETCH_OBJECT_DETAILS_FAILED);
        }
      } catch (error) {
        console.error("[FetchObjectDetails] Exception:", error);
        showErrorToast(MSG_FETCH_OBJECT_DETAILS_FAILED);
      } finally {
        dispatch(setLoading(false));
      }
    },
    [dispatch, handleObsolescenceWidget, showErrorToast]
  );

  const handleDrop = useCallback(
    async (dataItems) => {
      dispatch(setLoading(true));
      console.log("[handleDrop] Called with dataItems:", dataItems);
      try {
        if (dataItems && dataItems.length > 0) {
          await fetchObjectDetails(dataItems);
        } else {
          console.warn("[handleDrop] No data items to process.");
          dispatch(setLoading(false));
        }
      } catch (error) {
        console.error("[handleDrop] Unexpected error:", error);
        showErrorToast(MSG_UNEXPECTED_ERROR);
        dispatch(setLoading(false));
      }
    },
    [fetchObjectDetails, showErrorToast, dispatch]
  );

  const initializeDroppableArea = useCallback(() => {
    if (!isDropped) {
      console.log("[initializeDroppableArea] Resetting isDropped to false...");
      dispatch(setIsDroppedAction(false));
    } else {
      console.log(
        "[initializeDroppableArea] isDropped is already true. Skipping reset."
      );
    }

    const interval = setInterval(() => {
      const droppableContainer = document.querySelector(".droppable-container");
      if (droppableContainer) {
        clearInterval(interval);
        initDroppable(droppableContainer, handleDrop, dispatch, showErrorToast);
      }
    }, 100);

    return () => clearInterval(interval);
  }, [handleDrop, dispatch, isDropped, showErrorToast]);

  return {
    initializeDroppableArea,
    loading,
    handleDrop,
  };
};

export default useObsolescenceDropableArea;
