const useObsolescenceWidget = () => {
  const { showErrorToast } = useToast();
  const dispatch = useDispatch();
  const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;
  let email = window.widget.getValue("email");

  console.log("Email in useObsolescenceWidget:", email);
  // console.log("Security context using preferences are", securitycontext);

  // const {droppedObjectData}= useSelector ((state)=> state.droppedObjectData)
  // console.log("SSSSSSSSSSSSSSSs", droppedObjectData)

  const handleObsolescenceWidget = async (collabSpace,objectId, type, relativePath) => {
    if (!collabSpace || !objectId || !type || !relativePath) {
      console.error("[handleObsolescenceWidget] âŒ Missing required argument(s):", {
        collabSpace,
        objectId,
        type,
        relativePath,
      });
      return;
    }
    
    try {
      dispatch(
        setObselenceObjectData({ parentObjectData: [] })
      );
      let tableOfArrayData =[]
      dispatch(setParentDetailsLoading(true));

      const headers = await fetchCsrfToken();
      console.log("headerssss", headers);

      if (!headers) {
        showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
        return;
      }

      console.log("[useObsolescenceWidget] CSRF Headers:", headers);
      // ================ locate api
      let baseURL = `${ENOVIA_BASE_URL}/resources/v1/modeler/dseng/dseng:EngItem/locate`;
      let body = {
        referencedObjects: [
          {
            source: ENOVIA_BASE_URL,
            type: type,
            identifier: objectId,
            relativePath: relativePath,
          },
        ],
      };

      let ChildObject = await callEnoviaWebService(
        "POST",
        baseURL,
        body,
        headers
      );

      console.log("SpecDetails Response:", ChildObject);
      if (ChildObject.status && ChildObject.output) {
        let member =
          ChildObject?.output?.member[0]["dseng:EngInstance"]?.member;
          console.log("ðŸ” EngInstance members:", member);

        if (member?.length > 0) {
          let url = [];
          member &&
            member.map((items) => {
              url.push(
                items.parentObject?.source + items.parentObject?.relativePath
              );
            });
          console.log("URL", url);
          let revisionStateData = [];
          if (url) {
            const promises = url.map(async (apiPath) => {
              const revisionState = await callEnoviaWebService(
                "GET",
                apiPath,
                "",
                headers
              );
              if (revisionState) {
                revisionStateData = [
                  ...revisionStateData,
                  ...revisionState?.output?.member,
                ];
              }
            });
            await Promise.all(promises);
          }

          
          console.log("revisionStateData", revisionStateData);
          dispatch(
            setObselenceObjectData({ parentObjectData: revisionStateData })
          );
        } else {
          console.log("Not working");
          console.warn("âš ï¸ No parent object members found in locate call.");
        }
       
        if (type === "Change Action") {
          let isMFGCA = false;
          let ItemDetails = []; 
          console.log("The object Type is Change Action");
          const fetchChangeActionData = async () => {
            const CAURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/changeaction/${objectId}?%24fields=proposedChanges,realizedChanges`;
            const response = await callEnoviaWebService(
              "GET",
              CAURL,
              "",
              headers
            );
            console.log("Response from Change Action URL:", response);
            

            if (response.status && response.output) {
              // const realizedChanges = response.output.realizedChanges.map(
              //     (realizedChange) => realizedChange.where.identifier
              //   );
              
              for (let i = 0; i < response?.output?.proposedChanges?.length; i++) {
                const change = response.output.proposedChanges[i];
                const url = change.where.source + change.where.relativePath;
                console.log("Processing change:", change);
                console.log("URL for change:", url);
                const responseofTable = await callEnoviaWebService(
                  "GET",   
                  url,    
                  "", 
                  headers
                );  
                console.log("Response of Table:", responseofTable); 
                if(responseofTable.status){
                  console.log("Response of Table output:", responseofTable?.output); 
                  tableOfArrayData.push( {...responseofTable?.output?.member[0],changeIntent: change?.whats}
                  );
                }       
                // if(responseofTable.status && responseofTable.output){
                //   tableOfArrayData.push(responseofTable.output.member[0])

                // }
              }
              console.log("Table of Array Data:", tableOfArrayData);
              // const changeProcessingPromises =
              //   response.output.proposedChanges.map(async (change) => {
              //     let ItemId = change.where.identifier;
              //     let ItemType = change.where.type;
              //     console.log("Processing change:", change);

              //     if (change.target === "NewVersion") {
              //       const sLatestID = await getLatestRevision(
              //         ItemId,
              //         ItemType,
              //         headers
              //       );
              //       if (realizedChanges.includes(sLatestID)) {
              //         ItemId = sLatestID;
              //       }
              //     }
              //     if (
              //       ItemType !== "Raw_Material" &&
              //       ItemType !== "VPMReference"
              //     ) {
              //       isMFGCA = true;
              //       const mfgDetails = await getScopedItms(ItemId, headers);
              //       console.log("MFG Details are:", mfgDetails);
              //       ItemId = mfgDetails.identifier;
              //       ItemType = mfgDetails.type;
              //     }
              //     if (ItemId !== undefined && ItemType !== undefined) {
              //       try {
              //         const [ItemPlants, Iteminfo] = await Promise.all([
              //           getAssignedClasses(ItemId, headers),
              //           getItemDetails(ItemId, ItemType, headers),
              //         ]);

              //         ItemDetails.push({
              //           ItemId,
              //           ItemType,
              //           ItemState: Iteminfo?.member[0].state || "N/A",
              //           ItemTitle: Iteminfo?.member[0].title || "N/A",
              //           ItemMBOM:
              //             Iteminfo?.member[0]?.["dseno:EnterpriseAttributes"]
              //               ?.EMR_hasMBOM || "N/A",
              //           ItemPlants,
              //         });
              //       } catch (err) {
              //         console.error(
              //           `Error processing change for item ${ItemId}:`,
              //           err
              //         );
              //       }
              //     }
              //   });

              // await Promise.all(changeProcessingPromises);

            } else {
              console.error(
                "Invalid response structure or no proposed changes."
              );
            }
          };
          const CAAllPlantsData = async () => {
            // Replace this with your actual API call
            const allPlants = await getAllPlants(
              [collabSpace],
              headers,
              objectId
            ); // getAllPlants wants collabspace as an array.
            console.log("Parallel API response:", allPlants);
            dispatch(setCAAllPlants(allPlants));
          };

          // Run both functions in parallel
          await Promise.all([fetchChangeActionData(), CAAllPlantsData()]);

          dispatch(setParentDetailsLoading(false));
          dispatch(setCAItemDetails(ItemDetails));
          dispatch(setIsMFGCA(isMFGCA));
          dispatch(setCAHeaders(headers));
          dispatch(setObselenceObjectData({parentObjectData: tableOfArrayData}))
        } else {
          // Step 1: Fetch user group collaboration spaces
          const userGroupCollab = await getUserGroupCollab(
            headers,
            objectId,
            email
          );
          console.log("[Plant Assignment] User Groups:", userGroupCollab);

          let allCollabSpaces = [...userGroupCollab, collabSpace];
          console.log(
            "[Use Plant Assignment] All CollabSpaces:",
            allCollabSpaces
          );

          // Step 2: Fetch all plants if collab spaces exist
          let allPlants = [];
          if (allCollabSpaces.length > 0) {
            allPlants = await getAllPlants(allCollabSpaces, headers, objectId);
            console.log("[Use Plant Assignment] All Plants:", allPlants);
          } else {
            console.warn("[Use Plant Assignment] No CollabSpaces found.");
          }

          // Step 3: Fetch assigned plants if there are any
          if (allPlants.length > 0) {
            const plants = await fetchAssignedPlants(
              allPlants,
              headers,
              objectId
            );
            console.log("[Use Plant Assignment]: ", plants);

            if (plants.success) {
              dispatch(setObselenceObjectData(plants.data.plantData));
              dispatch(setHeaders(headers));
            } else {
              console.error("Failed to fetch plant data.");
            }
          } else {
            console.warn("[Use Plant Assignment] No Plants found.");
          }

          // Step 4: Fetch Product Children based on type
          let getProductChildren = {};
          if (type === "Raw_Material") {
            getProductChildren = { success: true, data: [] };
          } else {
            getProductChildren = await fetchProductChildren(
              headers,
              objectId,
              type
            );
          }

          console.log("Type After:", type);
          console.log(
            "[Use Plant Assignment] Product Children:",
            getProductChildren
          );

          if (getProductChildren.success) {
            dispatch(setProductChildren(getProductChildren.data));
          }

          // Step 5: Fetch Change Action details
          const getCaDetails = await fetchCADetails(headers, objectId, state);
          console.log("[Use Plant Assignment] CA Details:", getCaDetails);

          if (getCaDetails.success) {
            dispatch(setCAName(getCaDetails.data));
            dispatch(setProposedChanges(getCaDetails.proposedChanges));
            dispatch(setCAData(getCaDetails.CAData));
          }

          console.log("[Plant Assignment] All services executed successfully.");
        }
      }
    } catch (error) {
      console.error("[useObsolescenceWidget] Error:", error);
      throw error; // Let the caller handle the error
    } finally {
      dispatch(setParentDetailsLoading(false)); // Ensure loading state is reset
    }
  };

  return { handleObsolescenceWidget };
};

export default useObsolescenceWidget;

