import React, { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DragAndDropComponent from "../../components/DragAndDrop/DragAndDrop";
import { Image } from "react-bootstrap";
import "./ObsolescenceWidget.css";
import Loader from "../../components/Loader/Loader";
import ReusableTable from "../../components/Table/Table";
import CardWithDragAndDrop from "../../components/Card/cardwithdraganddrop";
import {
  setDroppedObjectData,
  setIsDropped,
  setPlantObjectData,
} from "../../store/droppedObjectSlice";
import {   formattedFinalMessage,
  getCardData,
  getTableData,
  getUniqueTableData,
  processErrorObj,
  tableColumns,
  uniqueColumns } from "./ObsolescencedataHelper";
import useToast from "../../hooks/useToast";
import { saveData } from "../../services/api/PlantAssignment/saveTableDataService";
import { MSG_SAVE_FAILURE, MSG_SAVE_SUCCESS } from "../../utils/toastMessages";
import useObsolescenceDropableArea from "../../hooks/useObsolescenceDropableArea";
import ObsolescenceWidgetToolbarNativeCta from "./ObsolescenceWidgetToolbarNativeCta";
import { fetchData } from "../../utils/helpers";

const ObsolescenceWidget = () => {
  const { initializeDroppableArea, loading } = useObsolescenceDropableArea();
  const [tableKey, setTableKey] = useState(0);
  const [tableData, setTableData] = useState([]);
  const [isTableLoading, setIsTableLoading] = useState(false);
  const [screenLoader, setScreenLoader] = useState(false);
  const [isCardDataAvailable, setIsCardDataAvailable] = useState(false);
  const [specData, setSpecData] = useState([]);
  const [selectableRows, setSelectableRows] = useState([]);
  const dispatch = useDispatch();
  const { showSuccessToast, showErrorToast } = useToast();

  // Get redux data
  const selectedTableRows = useSelector(
    (state) => state.droppedObject.selectedTableRows
  );
  const droppedObjectData = useSelector(
    (state) => state.droppedObject.droppedObjectData
  );
  const loadingParentDetails = useSelector(
    (state) => state.droppedObject.loadingParentDetails
  );
  const isDropped = useSelector((state) => state.droppedObject.isDropped);
  const bosData = useSelector((state) => state.droppedObject.BOSObjectData);
  const bosSpecDocument = useSelector(
    (state) => state.droppedObject.BOSObjectData?.specDocument || []
  );
  const parentObjectData = useSelector(
    (state) => state.droppedObject?.obselenceObjectData?.parentObjectData || []
  );

  // Derive type and state from droppedObjectData if present
  let state, type;
  if (droppedObjectData.cardData && droppedObjectData.initialDraggedData) {
    state = droppedObjectData.cardData["Maturity State"];
    type = droppedObjectData.initialDraggedData?.data?.items[0].objectType;
  }
  console.log("droppedObjectData.initialDraggedData?.data", droppedObjectData.initialDraggedData?.data)
  console.log("Type of Parent:", type);

  useEffect(() => {
    if (specData.length) {
      setTableData(specData.map((item) => ({ ...item, changedCells: {} })));
    }
  }, [specData]);

  useEffect(() => {
    if (!isDropped) {
      initializeDroppableArea();
    }
  }, [isDropped, initializeDroppableArea]);

  useEffect(() => {
    setIsTableLoading(loadingParentDetails);
  }, [loadingParentDetails]);

  const newTableData = useMemo(() => getTableData(parentObjectData, type), [
    parentObjectData,
    type,
  ]);

  useEffect(() => {
    setSpecData(newTableData);
  }, [newTableData]);

  useEffect(() => {
    if (newTableData.length >= 0) {
      setTableData(newTableData);
      setTableKey((prevKey) => prevKey + 1);
    }
  }, [newTableData]);

  const cardData = useMemo(() => getCardData(droppedObjectData), [
    droppedObjectData,
  ]);

  useEffect(() => {
    setIsCardDataAvailable(!!cardData);
  }, [cardData]);

  const columns = useMemo(
    () =>
      tableColumns(
        type,
        droppedObjectData?.cardData["Latest Revision"],
        droppedObjectData?.cardData["Dropped Revision"]
      ),
    [type, droppedObjectData?.cardData]
  );

  console.log("In Widget columns", columns)
  // ...rest of your functions like handleSave, handleMassUpdate, updateTableData etc.

  const handleHomeClick = () => {
    initializeDroppableArea();
    dispatch(setIsDropped(false));
    dispatch(
      setDroppedObjectData({
        cardData: {},
        parentDetails: [],
        versions: [],
        initialDraggedData: [],
      })
    );
    dispatch(
      setPlantObjectData({
        allPlants: [],
        initialAssignedPlants: [],
        uniquePlants: [],
        productChildren: [],
        CAName: false,
        headers: {},
      })
    );
    setTableData([]);
    setIsCardDataAvailable(false);
  };

  return (
    <>
      {screenLoader && (
        <div className="loading-overlay">
          <Loader />
          <p>Saving...</p>
        </div>
      )}
      {!isDropped && !loading && !isTableLoading && <DragAndDropComponent />}
      {loading && <Loader />}
      {isDropped && (
        <>
          {loading && <Loader />}
          <div className="content-wrapper py-3 border-bottom">
            <div className="d-flex ">
              <div className=" p-0 pt-4">
                <Image
                  src="https://thewhitechamaleon.github.io/testrapp/images/home.png"
                  alt="home-icon"
                  className="home-icon"
                  onClick={handleHomeClick}
                />
              </div>
              {cardData && (
                <CardWithDragAndDrop
                  data={cardData}
                  widgetType="ObsolescenceWidget"
                />
              )}
            </div>
          </div>

          {isTableLoading ? (
            <div className="loading-indicator mt-5">
              <Loader />
            </div>
          ) : (
            <div className="wrapper-cta">
              <ObsolescenceWidgetToolbarNativeCta
                onSave={() => console.log("Save function here")} // replace with handleSave
                type={type}
                latestRevision={droppedObjectData?.cardData["Latest Revision"]}
                droppedRevision={droppedObjectData?.cardData["Dropped Revision"]}
                selectedRows={selectedTableRows}
                state={state}
                tableData={selectableRows}
                onMassUpdate={() => console.log("Mass update clicked")} // replace with handleMassUpdate
              />
              <ReusableTable
                key={tableKey}
                data={tableData}
                columns={columns}
                meta={{ updateTableData: (data) => console.log("updateTableData called", data) }} // replace with updateTableData
                type={type}
                latestRevision={droppedObjectData?.cardData["Latest Revision"]}
                droppedRevision={droppedObjectData?.cardData["Dropped Revision"]}
                widgetType="Obsolescence_Attribute_Widget"
                disableSelection={true}  
              />
            </div>
          )}
        </>
      )}
    </>
  );
};

export default ObsolescenceWidget;
